@import url(https://fonts.googleapis.com/css?family=Arimo:400,400i|Sanchez:400,400i);
.hint {
  padding: 10px;
  background-color: rgba(95, 219, 95, 0.712);
  border: 1px solid #197219;
  margin-bottom: 10px;
  font-family: 'Arimo', sans-serif; }
  .hint p {
    color: #197219;
    font-style: italic;
    margin: 3px; }

.inner-container {
  width: 250px;
  border: 3px solid #1badba;
  box-shadow: 1px 7px 10px #aaa;
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin: 50px auto;
  background: #ffffff;
  text-align: center; }
  .inner-container .input-group {
    padding: .5em;
    text-align: right; }
    .inner-container .input-group .form-input {
      margin-left: .5em;
      border: none;
      border-bottom: 1px solid #000;
      background-color: inherit;
      font-family: 'Sanchez', serif; }
      .inner-container .input-group .form-input.invalid {
        border-color: red; }
      .inner-container .input-group .form-input:focus {
        outline: none; }
    .inner-container .input-group small {
      display: block;
      font-size: .3em;
      font-style: italic;
      color: red; }
  .inner-container .submit-button {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 5px;
    cursor: pointer;
    outline: none; }
    .inner-container .submit-button:hover {
      -webkit-transition: ease-in 300ms;
      transition: ease-in 300ms;
      background-color: #4fb7f3;
      color: #fff; }

.location-display {
  padding: 0;
  margin-top: 10px;
  text-align: center;
  font-family: 'Arimo', sans-serif; }
  .location-display h4, .location-display h3 {
    font-style: italic; }
  .location-display .success-location {
    padding: 10px;
    background-color: rgba(55, 153, 209, 0.712);
    border: 1px solid #135472;
    color: #135472; }
    .location-display .success-location h3 {
      color: #fff;
      font-family: 'Sanchez', sans-serif; }
  .location-display .invalid-location {
    padding: 10px;
    background-color: rgba(223, 136, 120, 0.712);
    border: 1px solid #b6221d;
    color: #b6221d; }

body {
  background-color: #c9c9c9;
  margin: 0;
  padding: 0;
  font-family: 'Arimo', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.container {
  width: 100%;
  height: 100%;
  padding-top: 5em;
  display: flex;
  align-items: center;
  flex-direction: column; }

